// 2020.7 LARE

import React, { Component } from 'react';
import '../styles/briefinfo.css';
import themePicker from '../theme/themeControl';
import info from '../static/briefinfo';
import fetchApi from '../api/callApi';
import ContentLoader from "react-content-loader";

class BriefInfo extends Component {
    constructor(props) {
        super(props);
        this.state = {
            theme: themePicker(),
            infoData: []
        }
    }

    renderData = () => {
        const { theme } = this.state;
        let dataList = [];
        info.data.map((item) => {
            dataList.push(
                <div key={item.key}>
                    <span
                        style={{
                            color: theme.color.primaryFont,
                            fontSize: theme.fontSize.text,
                            lineHeight: theme.lineHeight.text
                        }}
                    >{item.before}</span>
                    <div className="databox">
                        <span
                            style={{
                                color: theme.color.primaryFont,
                                fontSize: theme.fontSize.title,
                                lineHeight: theme.lineHeight.title
                            }}
                        >
                            {item.num}
                        </span>
                        <span
                            style={{
                                color: theme.color.primaryFont,
                                fontSize: theme.fontSize.subtitle,
                                lineHeight: theme.lineHeight.subtitle
                            }}
                        >{item.unit}</span>
                    </div>
                    <span
                        style={{
                            color: theme.color.primaryFont,
                            fontSize: theme.fontSize.text,
                            lineHeight: theme.lineHeight.text
                        }}
                    >
                        {item.after}
                    </span>
                </div>
            )
        })
        return dataList;
    }

    renderIntro = () => {
        const { theme, infoData } = this.state;
        // console.log(infoData)
        if (infoData.length > 0) {
            return infoData[0].content
        } else {
            return <ContentLoader
                speed={2}
                viewBox="0 0 1519 100"
                backgroundColor={theme.color.loadingBG}
                foregroundColor={theme.color.loadingWave}
            >
                <rect x="0" y="0" rx="5" ry="5" width="1600" height="20" />
            </ContentLoader>
        }
    }

    componentDidMount = () => {
        if (this.state.infoData.length <= 0) {
            fetchApi('twtSynopsis', { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    // console.log(data)
                    this.setState({
                        infoData: data
                    })
                })
        }
    }

    render() {
        const { theme } = this.state;
        return (
            <div
                className="briefinfo"
                style={{
                    background: theme.color.primaryBG
                }}
            >
                <div className="container">
                    <p
                        className="pre"
                        style={{
                            color: theme.color.secondFont,
                            fontSize: theme.fontSize.assist,
                            lineHeight: theme.lineHeight.assist
                        }}
                    >
                        我们是
                    </p>
                    <p
                        className="title"
                        style={{
                            color: theme.color.primary,
                            fontSize: theme.fontSize.subtitle,
                            lineHeight: theme.lineHeight.subtitle
                        }}
                    >
                        天外天工作室。
                    </p>
                    <p
                        className="intro"
                        style={{
                            color: theme.color.primaryFont,
                            fontSize: theme.fontSize.text,
                            lineHeight: theme.lineHeight.text
                        }}
                    >
                        {this.renderIntro()}
                    </p>
                    <div
                        className="datadisplay"
                    >
                        {this.renderData()}
                    </div>
                </div>
            </div>
        );
    }
}

export default BriefInfo;