// 2020.7 LARE

import React, { Component } from 'react';
import '../styles/portal.css';
import { Col } from 'react-bootstrap';
import portal from '../static/portal';
import themePicker from '../theme/themeControl';
import fetchApi from '../api/callApi';
import baseUrl from '../api/baseUrl';
import ContentLoader from "react-content-loader"

class Portal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            theme: themePicker(),
            portalData: []
        }
    }

    renderLink = () => {
        const { theme, portalData } = this.state;
        let linkList = [];
        if (portalData.length > 0) {
            portalData.map((item) => {
                linkList.push(
                    <Col key={item.title} xs={6} md={4} className="portalitem">
                        <a className="iconbox" href={item.target_url}>
                            <img
                                className="icon"
                                src={baseUrl + item.icon_url}
                                alt={item.title}
                            />
                        </a>
                        <a
                            className="portallabel"
                            style={{
                                color: theme.color.primaryFont,
                                fontSize: theme.fontSize.text,
                                lineHeight: theme.lineHeight.text
                            }}
                            href={item.target_url}
                        >{item.title}</a>
                    </Col>
                )
            })
        } else {
            for (let i = 0; i < 6; i++) {
                linkList.push(
                    <Col key={i} xs={6} md={4} className="portalitem">
                        <ContentLoader
                            speed={2}
                            viewBox="0 0 60 60"
                            backgroundColor="#f3f3f3"
                            foregroundColor="#ecebeb"
                        >
                            <rect x="5" y="5" rx="0" ry="0" width="50" height="50" />
                        </ContentLoader>
                    </Col>
                )
            }
        }
        return linkList;
    }

    componentDidMount = () => {
        // console.log(this.state.portalData)
        if (this.state.portalData.length <= 0) {
            fetchApi('link', { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    // console.log(data)
                    this.setState({
                        portalData: data
                    })
                })
        }
    }

    render() {
        const { theme } = this.state;
        return (
            <div className="portal">
                {this.renderLink()}
            </div>
        );
    }
}

export default Portal;