// Home->工作室活动
// 2020.7 LARE

import React, { Component } from 'react';
import '../styles/activity.css';
import themePicker from '../theme/themeControl';
import activity from '../static/activity';
import { Carousel } from 'react-bootstrap';
import fetchApi from '../api/callApi';
import ContentLoader from "react-content-loader";
import baseUrl from '../api/baseUrl';

// test
import testImg from '../test/activity';

class Activity extends Component {
    constructor(props) {
        super(props);
        this.state = {
            theme: themePicker(),
            activityData: []
        }
    }

    renderCarousel = () => {
        const { theme, activityData } = this.state;
        let imgList = [];
        if (activityData.length > 0) {
            activityData.map((item, index) => {
                if (item.image_url) {
                    imgList.push(
                        <Carousel.Item key={index}>
                            <a href={item.target_url}>
                                <img
                                    src={baseUrl + item.image_url}
                                    alt="activity"
                                    style={{ width: "100%" }}
                                />
                            </a>
                        </Carousel.Item>
                    )
                }
            })
        } else {
            imgList.push(
                <ContentLoader
                    key={0}
                    speed={2}
                    viewBox="0 0 760 326"
                    backgroundColor={theme.color.loadingBG}
                    foregroundColor={theme.color.loadingWave}
                >
                    <rect x="0" y="0" rx="5" ry="5" width="760" height="326" />
                </ContentLoader>
            )
        }
        return imgList;
    }

    componentDidMount = () => {
        const { activityData } = this.state;
        if (activityData.length <= 0) {
            fetchApi('activity', { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    // console.log(data)
                    this.setState({
                        activityData: data
                    })
                })
        }
    }

    render() {
        const { theme } = this.state;
        return (
            <div
                className="activity"
                style={{
                    background: theme.color.primaryBG
                }}
            >
                <div className="container">
                    <p
                        className="pre"
                        style={{
                            color: theme.color.secondFont,
                            fontSize: theme.fontSize.assist,
                            lineHeight: theme.lineHeight.assist
                        }}
                    >我们拥有</p>
                    <p
                        className="title"
                        style={{
                            color: theme.color.primary,
                            fontSize: theme.fontSize.subtitle,
                            lineHeight: theme.lineHeight.subtitle
                        }}
                    >丰富多彩的活动。</p>
                    <p
                        className="intro"
                        style={{
                            color: theme.color.primaryFont,
                            fontSize: theme.fontSize.text,
                            lineHeight: theme.lineHeight.text
                        }}
                    >{activity.intro}</p>
                    <div
                        className="photodisplay"
                    >
                        <Carousel controls={false}>
                            {this.renderCarousel()}
                        </Carousel>
                    </div>
                </div>
            </div>
        );
    }
}

export default Activity;