// Home->通知公告
// 2020.7 LARE

import React, { Component } from 'react';
import '../styles/notice.css';
import themePicker from '../theme/themeControl';
import { Col } from 'react-bootstrap';
import Portal from './Portal';
// import notice from '../test/notice';
import fetchApi from '../api/callApi';
import ContentLoader from "react-content-loader";

const noticeTab = [
    {
        "tabName": "最新通知",
        "id": 1,
    }, {
        "tabName": "理论答题",
        "id": 2,
    }, {
        "tabName": "党建系统",
        "id": 3,
    }
]

class Notice extends Component {
    constructor(props) {
        super(props);
        this.state = {
            currentTab: 0,
            theme: themePicker(),
            noticeData1: [],
            noticeData2: [],
            noticeData3: [],
        }
    }

    handleClickTab = (e) => {
        this.setState({
            currentTab: e
        })
    }

    renderTab = () => {
        const { theme } = this.state;
        let tabList = [];
        noticeTab.map((item, index) => {
            tabList.push(
                <span
                    key={index}
                    className="noticetabname"
                    onClick={() => this.handleClickTab(index)}
                    style={this.state.currentTab === index ?
                        {
                            color: theme.color.primary,
                            fontSize: theme.fontSize.text,
                            lineHeight: theme.lineHeight.text,
                            borderBottom: `${theme.color.primary} 2px solid`
                        } :
                        {
                            color: theme.color.primaryFont,
                            fontSize: theme.fontSize.text,
                            lineHeight: theme.lineHeight.text,
                        }
                    }
                >
                    {item.tabName}
                </span>
            )
        })
        return tabList;
    }

    renderNoticePane = (list) => {
        const { theme } = this.state;
        let noticePaneList = [];
        if (list.length > 0) {
            noticePaneList.push(
                <a
                    key={0}
                    href={`/article?labelId=${list[0].label_id}&id=${list[0].id}`}
                    className="listitem"
                    style={{
                        color: theme.color.primaryFont,
                        fontSize: theme.fontSize.subtitle,
                        lineHeight: theme.lineHeight.subtitle,
                    }}
                >
                    {list[0].title}
                    <span
                        style={{
                            margin: "0 0 0 0.5rem",
                            float: "right",
                            color: theme.color.secondFont,
                            fontSize: theme.fontSize.text,
                            lineHeight: theme.lineHeight.subtitle,
                        }}
                    >
                        {list[0].time.substr(0, 11)}
                    </span>
                    <span
                        style={{
                            margin: "0 0 0 0.5rem",
                            float: "right",
                            color: theme.color.secondFont,
                            fontSize: theme.fontSize.text,
                            lineHeight: theme.lineHeight.subtitle,
                        }}
                    >
                        {list[0].source}
                    </span>
                </a>
            )
            for (let i = 1; i < Math.min(list.length, 7); i++) {
                noticePaneList.push(
                    <a
                        key={i}
                        href={`/article?labelId=${list[i].label_id}&id=${list[i].id}`}
                        className="listitem"
                        style={{
                            color: theme.color.primaryFont,
                            fontSize: theme.fontSize.text,
                            lineHeight: theme.lineHeight.text,
                        }}
                    >
                        {list[i].title}
                        <span
                            style={{
                                margin: "0 0 0 0.5rem",
                                float: "right",
                                color: theme.color.secondFont,
                                fontSize: theme.fontSize.text,
                                lineHeight: theme.lineHeight.text,
                            }}
                        >
                            {list[i].time.substr(0, 11)}
                        </span>
                        <span
                            style={{
                                margin: "0 0 0 0.5rem",
                                float: "right",
                                color: theme.color.secondFont,
                                fontSize: theme.fontSize.text,
                                lineHeight: theme.lineHeight.text,
                            }}
                        >
                            {list[i].source}
                        </span>
                    </a>
                )
            }

        } else {
            return <ContentLoader
                speed={2}
                viewBox="0 0 600 255"
                backgroundColor={theme.color.loadingBG}
                foregroundColor={theme.color.loadingWave}
            >
                <rect x="0" y="12" rx="5" ry="5" width="600" height="30" />
                <rect x="0" y="64" rx="5" ry="5" width="600" height="20" />
                <rect x="0" y="107" rx="5" ry="5" width="600" height="20" />
                <rect x="0" y="153" rx="5" ry="5" width="600" height="20" />
                <rect x="0" y="198" rx="5" ry="5" width="600" height="20" />
            </ContentLoader>
        }
        return noticePaneList;
    }

    componentDidMount = () => {
        const { noticeData1, noticeData2, noticeData3 } = this.state;
        if (noticeData1.length <= 0) {
            fetchApi(`notice`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        noticeData1: data
                    })
                })
        }
        if (noticeData2.length <= 0) {
            fetchApi(`notice/1`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        noticeData2: data
                    })
                })
        }
        if (noticeData3.length <= 0) {
            fetchApi(`notice/2`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        noticeData3: data
                    })
                })
        }
        // console.log(noticeData1)
        // console.log(noticeData2)
        // console.log(noticeData3)
    }

    render() {
        const { theme } = this.state;
        return (
            <div
                className="notice"
                style={{
                    background: theme.color.primaryBG
                }}
            >
                <div className="container">
                    <Col xs={9} md={6} className="noticepart">
                        <div className="noticemenu">
                            <span
                                className="title"
                                style={{
                                    color: theme.color.primary,
                                    fontSize: theme.fontSize.subtitle,
                                    lineHeight: theme.lineHeight.subtitle
                                }}
                            >通知公告</span>
                            <div className="noticetabs">
                                {this.renderTab()}
                            </div>
                        </div>
                        <div className="noticepane">
                            <div
                                className="noticelist"
                                style={this.state.currentTab === 0 ? {} : { display: "none" }}
                            >
                                {this.renderNoticePane(this.state.noticeData1)}
                            </div>
                            <div
                                className="noticelist"
                                style={this.state.currentTab === 1 ? {} : { display: "none" }}
                            >
                                {this.renderNoticePane(this.state.noticeData2)}
                            </div>
                            <div
                                className="noticelist"
                                style={this.state.currentTab === 2 ? {} : { display: "none" }}
                            >
                                {this.renderNoticePane(this.state.noticeData3)}
                            </div>
                        </div>
                    </Col>
                    <Col xs={9} md={6}
                        className="protalicons"
                    >
                        <Portal />
                    </Col>
                </div>
            </div>
        );
    }
}

export default Notice;