// Home->加入我们
// 2020.7 LARE

import React, { Component } from 'react';
import '../styles/joinus.css';
import image from '../assets/joinus.jpeg';
import themePicker from '../theme/themeControl';
class JoinUs extends Component {
    constructor(props) {
        super(props);
        this.state = {
            theme: themePicker()
        }
    }
    render() {
        const { theme } = this.state;
        return (
            <div
                className="joinus"
                style={{
                    background: `url(${image})`,
                    backgroundRepeat: "no-repeat",
                    backgroundPosition: "center",
                    backgroundSize: "cover",
                }}
            >
                <div
                    className="content"
                    style={{
                        color: theme.color.invertFont,
                        background: theme.color.brand,
                    }}
                >
                    <div
                        className="title"
                        style={{
                            fontSize: theme.fontSize.subtitle,
                            lineHeight: theme.lineHeight.subtitle,
                        }}
                    >
                        加入我们
                     </div>
                    <div
                        className="subtitle"
                        style={{
                            fontSize: theme.fontSize.text,
                            lineHeight: theme.lineHeight.text,
                        }}
                    >
                        观看天外天工作室纳新视频
                        </div>
                    <div
                        className="joinusBtn"
                        style={{
                            background: theme.color.invertBG,
                            color: theme.color.invertFont,
                            fontSize: theme.fontSize.text,
                            lineHeight: theme.lineHeight.text
                        }}
                    >
                        <a href="https://recruit.twtstudio.com/">
                            前往招募中心
                        </a>
                    </div>
                </div>
            </div>
        );
    }
}

export default JoinUs;