import React, { Component } from 'react';
import { Col, Row, Container } from 'react-bootstrap';
import themePicker from '../theme/themeControl';
import Header from '../components/Header';
import Footer from '../components/Footer';
import Portal from '../components/Portal';
import '../styles/article.css';
// import article from '../test/article';
import fetchApi from '../api/callApi';
import ContentLoader from "react-content-loader";


class Article extends Component {
    constructor(props) {
        super(props);
        this.state = {
            theme: themePicker(),
            articleData: []
        }
    }

    getQueryVariable(variable) {
        var query = window.location.search.substring(1);
        var vars = query.split("&");
        for (var i = 0; i < vars.length; i++) {
            var pair = vars[i].split("=");
            if (pair[0] == variable) { return pair[1]; }
        }
        return (false);
    }

    componentDidMount = () => {
        const { articleData } = this.state;
        let articleId = this.getQueryVariable("id");
        if (articleData.length <= 0) {
            fetchApi(`noticeDetails/${articleId}`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        articleData: data
                    })
                })
        }
    }

    render() {
        const { theme, articleData } = this.state;
        return (
            <div
                className="article"
                style={{
                    background: theme.color.primaryBG
                }}
            >
                <Header />
                <Container fluid>
                    <Row className="articlecontent">
                        {articleData.length > 0 ?
                            <Col xs={9} md={9}>
                                <div
                                    className="title"
                                    style={{
                                        color: theme.color.primaryFont,
                                        fontSize: theme.fontSize.title,
                                        lineHeight: theme.lineHeight.title
                                    }}
                                >
                                    {articleData[0].title}
                                </div>
                                <span
                                    className="time"
                                    style={{
                                        marginRight: "1rem",
                                        color: theme.color.secondFont,
                                        fontSize: theme.fontSize.assist,
                                        lineHeight: theme.lineHeight.assist
                                    }}
                                >
                                    {articleData[0].time.substr(0, 10)}
                                </span>
                                <span
                                    className="source"
                                    style={{
                                        marginRight: "1rem",
                                        color: theme.color.secondFont,
                                        fontSize: theme.fontSize.assist,
                                        lineHeight: theme.lineHeight.assist
                                    }}
                                >
                                    {articleData.source}
                                </span>
                                <div className="content"
                                    dangerouslySetInnerHTML={{ __html: articleData[0].content }}
                                    style={{
                                        color: theme.color.primaryFont,
                                        fontSize: theme.fontSize.text,
                                        lineHeight: theme.lineHeight.text
                                    }}
                                >
                                </div>
                            </Col>
                            : <Col xs={9} md={9}>
                                <ContentLoader
                                    speed={2}
                                    viewBox="0 0 1600 100"
                                    backgroundColor={theme.color.loadingBG}
                                    foregroundColor={theme.color.loadingWave}
                                >
                                    <rect x="400" y="24" rx="5" ry="5" width="800" height="30" />
                                    <rect x="500" y="74" rx="5" ry="5" width="600" height="20" />
                                    <rect x="500" y="114" rx="5" ry="5" width="600" height="20" />
                                </ContentLoader>
                            </Col>}
                        <Col xs={3} md={3} className="portalpart">
                            <div
                                className="sider"
                                style={{
                                    color: theme.color.primaryFont,
                                    fontSize: theme.fontSize.subtitle,
                                    lineHeight: theme.lineHeight.subtitle
                                }}
                            >快速入口</div>
                            <Portal />
                        </Col>
                    </Row>
                </Container>
                <Footer />
            </div>
        );
    }
}

export default Article;