// Home->各组介绍
// 2020.7 LARE

import React, { Component } from 'react';
import '../styles/group.css';
import themePicker from '../theme/themeControl';
import group from '../static/group';
import fetchApi from '../api/callApi';
import baseUrl from '../api/baseUrl';
import ContentLoader from "react-content-loader";

class Group extends Component {
    constructor(props) {
        super(props);
        this.state = {
            currentTab: 0,
            theme: themePicker(),
            groupData1: [],
            groupData2: [],
            groupData3: [],
            groupData4: [],
            groupData5: []
        }
    }

    handleClickTab = (e) => {
        this.setState({
            currentTab: e
        })
    }

    validImage = (str, key) => {
        const { theme, currentTab } = this.state;
        var ImgObj = new Image();
        // ImgObj.src = baseUrl + key;
        if (str.length>0) {
            return <img
                src={baseUrl + str}
                key={key}
                className="grouppic"
                style={currentTab === key ? {} : { display: "none" }}
            />
        } else {
            return <ContentLoader
                speed={2}
                viewBox="0 0 760 326"
                style={currentTab !== key ? { display: "none" } : {}}
                backgroundColor={theme.color.loadingBG}
                foregroundColor={theme.color.loadingWave}
            >
                <rect x="0" y="0" rx="5" ry="5" width="760" height="326" />
            </ContentLoader>
        }
    }

    validIntro = (str, key) => {
        const { theme, currentTab } = this.state;
        if (str.length > 0) {
            return <div
                key={key}
                className="groupintro"
                style={currentTab === key ?
                    {
                        color: theme.color.primaryFont,
                        fontSize: theme.fontSize.text,
                        lineHeight: theme.lineHeight.text
                    } : { display: "none" }}
            >
                {str}
            </div>
        } else {
            return <ContentLoader
                speed={2}
                viewBox="0 0 1600 100"
                backgroundColor={theme.color.loadingBG}
                foregroundColor={theme.color.loadingWave}
            >
                <rect x="400" y="24" rx="5" ry="5" width="800" height="20" />
                <rect x="500" y="64" rx="5" ry="5" width="600" height="20" />
            </ContentLoader>
        }
    }

    validTab = (str, key) => {
        const { currentTab, theme } = this.state;
        if (str.length > 0) {
            return <span
                key={key}
                className="groupname"
                onClick={() => this.handleClickTab(key)}
                style={currentTab === key ?
                    {
                        color: theme.color.primary,
                        fontSize: theme.fontSize.text,
                        lineHeight: theme.lineHeight.text,
                        borderBottom: `${theme.color.primary} 2px solid`
                    } :
                    {
                        color: theme.color.primaryFont,
                        fontSize: theme.fontSize.text,
                        lineHeight: theme.lineHeight.text,
                    }
                }
            >
                {str}
            </span>
        } else {
            return <span
                key={key}
                className="groupname"
                onClick={() => this.handleClickTab(key)}
                style={currentTab === key ?
                    {
                        color: theme.color.primary,
                        fontSize: theme.fontSize.text,
                        lineHeight: theme.lineHeight.text,
                        borderBottom: `${theme.color.primary} 2px solid`
                    } :
                    {
                        color: theme.color.primaryFont,
                        fontSize: theme.fontSize.text,
                        lineHeight: theme.lineHeight.text,
                    }
                }
            >
                未命名
        </span>
        }
    }

    renderTab = () => {
        const { groupData1, groupData2, groupData3, groupData4, groupData5 } = this.state;
        let tabList = [];
        if (groupData1.length > 0) {
            tabList.push(
                this.validTab(groupData1[0].name, 0)
            )
        }
        if (groupData2.length > 0) {
            tabList.push(
                this.validTab(groupData2[0].name, 1)
            )
        }
        if (groupData3.length > 0) {
            tabList.push(
                this.validTab(groupData3[0].name, 2)
            )
        }
        if (groupData4.length > 0) {
            tabList.push(
                this.validTab(groupData4[0].name, 3)
            )
        }
        if (groupData5.length > 0) {
            tabList.push(
                this.validTab(groupData5[0].name, 4)
            )
        }
        return tabList;
    }

    renderTabPane = () => {
        const { groupData1, groupData2, groupData3, groupData4, groupData5 } = this.state;
        let tabPaneList = [];
        if (groupData1.length > 0) {
            tabPaneList.push(
                this.validIntro(groupData1[0].introduction, 0)
            )
        }
        if (groupData2.length > 0) {
            tabPaneList.push(
                this.validIntro(groupData2[0].introduction, 1)
            )
        }
        if (groupData3.length > 0) {
            tabPaneList.push(
                this.validIntro(groupData3[0].introduction, 2)
            )
        }
        if (groupData4.length > 0) {
            tabPaneList.push(
                this.validIntro(groupData4[0].introduction, 3)
            )
        }
        if (groupData5.length > 0) {
            tabPaneList.push(
                this.validIntro(groupData5[0].introduction, 4)
            )
        }
        return tabPaneList;
    }

    renderPicPane = () => {
        const { theme, groupData1, groupData2, groupData3, groupData4, groupData5 } = this.state;
        let picPaneList = [];
        if (groupData1.length > 0) {
            picPaneList.push(
                this.validImage(groupData1[0].image_url, 0)
            )
        }
        if (groupData2.length > 0) {
            picPaneList.push(
                this.validImage(groupData2[0].image_url, 1)
            )
        }
        if (groupData3.length > 0) {
            picPaneList.push(
                this.validImage(groupData3[0].image_url, 2)
            )
        }
        if (groupData4.length > 0) {
            picPaneList.push(
                this.validImage(groupData4[0].image_url, 3)
            )
        }
        if (groupData5.length > 0) {
            picPaneList.push(
                this.validImage(groupData5[0].image_url, 4)
            )
        }
        return picPaneList;
    }

    componentDidMount = () => {
        const { groupData1, groupData2, groupData3, groupData4, groupData5 } = this.state;
        if (groupData1.length <= 0) {
            fetchApi(`groupSynopsis/1`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        groupData1: data
                    })
                })
        }
        if (groupData2.length <= 0) {
            fetchApi(`groupSynopsis/2`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        groupData2: data
                    })
                })
        }
        if (groupData3.length <= 0) {
            fetchApi(`groupSynopsis/3`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        groupData3: data
                    })
                })
        }
        if (groupData4.length <= 0) {
            fetchApi(`groupSynopsis/4`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        groupData4: data
                    })
                })
        }
        if (groupData5.length <= 0) {
            fetchApi(`groupSynopsis/5`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        groupData5: data
                    })
                })
        }
    }

    render() {
        const { theme } = this.state;
        return (
            <div
                className="group"
                style={{
                    background: theme.color.primaryBG
                }}
            >
                <div className="container">
                    <div className="picture">
                        {this.renderPicPane()}
                    </div>
                    <div className="groups">
                        {this.renderTab()}
                    </div>
                    <div className="intros">
                        {this.renderTabPane()}
                    </div>
                </div>
            </div>
        );
    }
}

export default Group;