import React, { Component } from 'react';
import { Col, Carousel, Container, OverlayTrigger, Popover } from 'react-bootstrap';
import themePicker from '../theme/themeControl';
import Header from '../components/Header';
import Footer from '../components/Footer';
// import whereabout from '../assets/whereabout.jpg';
import '../styles/member.css';
import fetchApi from '../api/callApi';
import ContentLoader from "react-content-loader";
import group from '../static/group';
import member from '../test/member';
import baseUrl from '../api/baseUrl';

class Member extends Component {
    constructor(props) {
        super(props);
        this.state = {
            currentTab: 0,
            theme: themePicker(),
            pictureData: [],
            groupData1: [],
            groupData2: [],
            groupData3: [],
            groupData4: [],
            groupData5: [],
            memberData1: [],
            memberData2: [],
            memberData3: [],
            memberData4: [],
            memberData5: [],
        }
    }

    infoPopover = (name, str) => {
        return (
            <Popover>
                <Popover.Title as="h3">{name}</Popover.Title>
                <Popover.Content>{str}</Popover.Content>
            </Popover>
        )
    }

    validImage = (str) => {
        var ImgObj = new Image();
        ImgObj.src = str;
        if (ImgObj.fileSize > 0 || (ImgObj.width > 0 && ImgObj.height > 0)) {
            return true
        } else {
            return false
        }
    }

    handleClickTab = (e) => {
        this.setState({
            currentTab: e
        })
    }

    validTab = (str, key) => {
        const { currentTab, theme } = this.state;
        if (str.length > 0) {
            return <span
                key={key}
                className="groupname"
                onClick={() => this.handleClickTab(key)}
                style={currentTab === key ?
                    {
                        color: theme.color.primary,
                        fontSize: theme.fontSize.text,
                        lineHeight: theme.lineHeight.text,
                        borderBottom: `${theme.color.primary} 2px solid`
                    } :
                    {
                        color: theme.color.primaryFont,
                        fontSize: theme.fontSize.text,
                        lineHeight: theme.lineHeight.text,
                    }
                }
            >
                {str}
            </span>
        } else {
            return <span
                key={key}
                className="groupname"
                onClick={() => this.handleClickTab(key)}
                style={currentTab === key ?
                    {
                        color: theme.color.primary,
                        fontSize: theme.fontSize.text,
                        lineHeight: theme.lineHeight.text,
                        borderBottom: `${theme.color.primary} 2px solid`
                    } :
                    {
                        color: theme.color.primaryFont,
                        fontSize: theme.fontSize.text,
                        lineHeight: theme.lineHeight.text,
                    }
                }
            >
                未命名
        </span>
        }
    }

    validIntro = (str, key) => {
        const { theme, currentTab } = this.state;
        if (str.length > 0) {
            return <div
                key={key}
                className="groupintro"
                style={currentTab === key ?
                    {
                        color: theme.color.primaryFont,
                        fontSize: theme.fontSize.text,
                        lineHeight: theme.lineHeight.text
                    } : { display: "none" }}
            >
                {str}
            </div>
        } else {
            return <ContentLoader
                speed={2}
                viewBox="0 0 1600 100"
                backgroundColor={theme.color.loadingBG}
                foregroundColor={theme.color.loadingWave}
            >
                <rect x="400" y="24" rx="5" ry="5" width="800" height="20" />
                <rect x="500" y="64" rx="5" ry="5" width="600" height="20" />
            </ContentLoader>
        }
    }

    renderTab = () => {
        const { groupData1, groupData2, groupData3, groupData4, groupData5 } = this.state;
        let tabList = [];
        if (groupData1.length > 0) {
            tabList.push(
                this.validTab(groupData1[0].name, 0)
            )
        }
        if (groupData2.length > 0) {
            tabList.push(
                this.validTab(groupData2[0].name, 1)
            )
        }
        if (groupData3.length > 0) {
            tabList.push(
                this.validTab(groupData3[0].name, 2)
            )
        }
        if (groupData4.length > 0) {
            tabList.push(
                this.validTab(groupData4[0].name, 3)
            )
        }
        if (groupData5.length > 0) {
            tabList.push(
                this.validTab(groupData5[0].name, 4)
            )
        }
        return tabList;
    }

    renderTabPane = () => {
        const { groupData1, groupData2, groupData3, groupData4, groupData5 } = this.state;
        let tabPaneList = [];
        if (groupData1.length > 0) {
            tabPaneList.push(
                this.validIntro(groupData1[0].introduction, 0)
            )
        }
        if (groupData2.length > 0) {
            tabPaneList.push(
                this.validIntro(groupData2[0].introduction, 1)
            )
        }
        if (groupData3.length > 0) {
            tabPaneList.push(
                this.validIntro(groupData3[0].introduction, 2)
            )
        }
        if (groupData4.length > 0) {
            tabPaneList.push(
                this.validIntro(groupData4[0].introduction, 3)
            )
        }
        if (groupData5.length > 0) {
            tabPaneList.push(
                this.validIntro(groupData5[0].introduction, 4)
            )
        }
        return tabPaneList;
    }

    renderPicPane = () => {
        const { theme, pictureData } = this.state;
        let imgList = [];
        console.log(pictureData)
        if (pictureData.length > 0) {
            for (let i = 0; i < pictureData.length; i++) {
                imgList.push(
                    <Carousel.Item key={i}>
                        <img
                            src={baseUrl + pictureData[i].image_url}
                            alt="activity"
                            style={{ width: "100%" }}
                        />
                    </Carousel.Item>
                )
            }
        } else {
            imgList.push(
                <ContentLoader
                    key={0}
                    speed={2}
                    viewBox="0 0 760 326"
                    backgroundColor={theme.color.loadingBG}
                    foregroundColor={theme.color.loadingWave}
                >
                    <rect x="0" y="0" rx="5" ry="5" width="760" height="326" />
                </ContentLoader>
            )
        }
        return imgList;
    }

    validMember = (key, data) => {
        const { theme } = this.state;
        if (data.image_url.length > 0) {
            return <Col
                xs={9} md={6}
                key={key}
                className="membercard"
            >
                <Col
                    xs={3} md={3}
                    className="memberavatar"
                >
                    <img src={baseUrl + data.image_url} alt="Member" />
                </Col>
                <Col
                    xs={9} md={9}
                >
                    <p
                        style={{
                            color: theme.color.primaryFont,
                            fontSize: theme.fontSize.subtitle,
                            lineHeight: theme.lineHeight.subtitle
                        }}
                    >{data.name}</p>
                    {/* 缺了点东西 */}
                    {/* <p
                        style={{
                            color: theme.color.primaryFont,
                            fontSize: theme.fontSize.text,
                            lineHeight: theme.lineHeight.text
                        }}
                    >{}</p> */}
                    <p
                        style={{
                            color: theme.color.primaryFont,
                            fontSize: theme.fontSize.text,
                            lineHeight: theme.lineHeight.text
                        }}
                    >{data.grade + " " + data.whereabout}</p>
                    <p
                        style={{
                            color: theme.color.primaryFont,
                            fontSize: theme.fontSize.text,
                            lineHeight: theme.lineHeight.text
                        }}
                    >{data.college + " " + data.major}</p>
                    <OverlayTrigger trigger="hover" placement="auto" overlay={this.infoPopover(data.name, data.introduction)}>
                        <p
                            style={{
                                width: "fit-content",
                                paddingRight: "0.5rem",
                                color: theme.color.primary,
                                fontSize: theme.fontSize.text,
                                lineHeight: theme.lineHeight.text
                            }}
                        >个人介绍</p>
                    </OverlayTrigger>
                </Col>
            </Col>
        }
    }

    renderMember = (data) => {
        let memberList = [];
        if (data.length > 0) {
            data.map((item, index) => {
                memberList.push(
                    this.validMember(index, item)
                )
            })
        }
        return memberList;
    }

    componentDidMount = () => {
        const { pictureData } = this.state;
        if (pictureData.length <= 0) {
            fetchApi('leagueBuilding', { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    // console.log(data)
                    this.setState({
                        pictureData: data
                    })
                })
        }
        const { groupData1, groupData2, groupData3, groupData4, groupData5
            , memberData1, memberData2, memberData3, memberData4, memberData5
        } = this.state;
        if (groupData1.length <= 0) {
            fetchApi(`groupSynopsis/1`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        groupData1: data
                    })
                })
        }
        if (groupData2.length <= 0) {
            fetchApi(`groupSynopsis/2`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        groupData2: data
                    })
                })
        }
        if (groupData3.length <= 0) {
            fetchApi(`groupSynopsis/3`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        groupData3: data
                    })
                })
        }
        if (groupData4.length <= 0) {
            fetchApi(`groupSynopsis/4`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        groupData4: data
                    })
                })
        }
        if (groupData5.length <= 0) {
            fetchApi(`groupSynopsis/5`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        groupData5: data
                    })
                })
        }
        if (memberData1.length <= 0) {
            fetchApi(`member/1`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        memberData1: data
                    })
                })
        }
        if (memberData2.length <= 0) {
            fetchApi(`member/2`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        memberData2: data
                    })
                })
        }
        if (memberData3.length <= 0) {
            fetchApi(`member/3`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        memberData3: data
                    })
                })
        }
        if (memberData4.length <= 0) {
            fetchApi(`member/4`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        memberData4: data
                    })
                })
        }
        if (memberData5.length <= 0) {
            fetchApi(`member/5`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        memberData5: data
                    })
                })
        }
    }

    render() {
        const { theme, currentTab } = this.state;
        return (
            <div
                className="member"
                style={{
                    background: theme.color.primaryBG
                }}
            >
                <Header />
                <Container fluid>
                    <div className="container">
                        <Carousel className="picture" >
                            {this.renderPicPane()}
                        </Carousel>
                        <div className="groups">
                            {this.renderTab()}
                        </div>
                        <div className="intros">
                            {this.renderTabPane()}
                        </div>
                        <div className="members" style={currentTab === 0 ? {} : { display: "none" }}>
                            {this.renderMember(this.state.memberData1)}
                        </div>
                        <div className="members" style={currentTab === 1? {} : { display: "none" }}>
                            {this.renderMember(this.state.memberData2)}
                        </div>
                        <div className="members" style={currentTab === 2 ? {} : { display: "none" }}>
                            {this.renderMember(this.state.memberData3)}
                        </div>
                        <div className="members" style={currentTab === 3 ? {} : { display: "none" }}>
                            {this.renderMember(this.state.memberData4)}
                        </div>
                        <div className="members" style={currentTab === 4 ? {} : { display: "none" }}>
                            {this.renderMember(this.state.memberData5)}
                        </div>
                    </div>
                </Container>
                <Footer />
            </div>
        );
    }
}

export default Member;