function themePicker() {
    // console.log(theme)
    let theme = sessionStorage.getItem("theme");
    switch (theme) {
        case "1":
            return twt_default
        case "2":
            return twt_dark
        case "3":
            return twt_awesome
        default:
            return twt_default
    }
}

export default themePicker;