import React, { Component } from 'react';
import { Col } from 'react-bootstrap';
import '../styles/footer.css';
import themePicker from '../theme/themeControl';
import footerData from '../static/footer';
import qrcode from '../assets/qrcode.png';

class Footer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            theme: themePicker()
        }
    }

    renderFooter = () => {
        const { theme } = this.state;
        let footerList = [];
        footerData.map((column, index) => {
            footerList.push(
                <Col
                    key={index}
                    xs={3} md={3}
                    className="contentlist"
                >
                    <div
                        className="footer-title"
                        style={{
                            fontSize: theme.fontSize.subtitle,
                            lineHeight: theme.lineHeight.subtitle
                        }}
                    >
                        {column.title}
                    </div>
                    {column.items.map((item, index) => {
                        if (item.link) {
                            return (
                                <a
                                    key={index}
                                    className="footer-item"
                                    href={item.link}
                                    style={{
                                        fontSize: theme.fontSize.text,
                                        lineHeight: theme.lineHeight.text
                                    }}
                                >
                                    {item.title}
                                </a>
                            )
                        } else {
                            return (
                                <p
                                    key={index}
                                    className="footer-item"
                                    style={{
                                        fontSize: theme.fontSize.text,
                                        lineHeight: theme.lineHeight.text
                                    }}
                                >
                                    {item.title}
                                </p>
                            )
                        }
                    })}
                </Col>
            );
        })
        return footerList;
    }

    render() {
        const { theme } = this.state;
        return (
            <div className="footer" style={{ background: theme.color.invertBG }} >
                <div className="content" style={{ color: theme.color.invertFont }}>
                    {this.renderFooter()}
                    <Col xs={3} md={3} className="contentlist">
                        <div
                            className="footer-title"
                            style={{
                                fontSize: theme.fontSize.subtitle,
                                lineHeight: theme.lineHeight.subtitle
                            }}
                        >
                            关注我们
                            </div>
                        <img
                            src={qrcode}
                            alt="QRcode"
                            className="qrcode"
                        />
                    </Col>
                </div>
                <div className="copyright"
                    style={{
                        color: theme.color.invertFont,
                        fontSize: theme.fontSize.text,
                        lineHeight: theme.lineHeight.text
                    }} >
                    ©Copyright 2020  All Rights Reserved
                </div>
            </div>
        );
    }
}

export default Footer;