// 2020.7 LARE

import React, { Component } from 'react';
import { Carousel } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import themePicker from '../theme/themeControl';
// import banner from '../test/banner';
import '../styles/banner.css';
import baseUrl from '../api/baseUrl';
import fetchApi from '../api/callApi';
import ContentLoader from "react-content-loader";

class Banner extends Component {
    constructor(props) {
        super(props);
        this.state = {
            theme: themePicker(),
            bannerData: []
        }
    }

    validImage = (str, alt) => {
        const { theme } = this.state;
        var ImgObj = new Image();
        ImgObj.src = (baseUrl + str);
        console.log(ImgObj.src)
        if (str.length > 0) {
            return <img
                className="banneritem"
                src={baseUrl + str}
                alt={alt}
            />
        } else {
            return <ContentLoader
                speed={2}
                viewBox="0 0 1519 474"
                backgroundColor={theme.color.loadingBG}
                foregroundColor={theme.color.loadingWave}
            >
                <rect x="0" y="0" rx="0" ry="0" width="1519" height="474" />
            </ContentLoader>
        }
    }

    renderBanner = () => {
        const { theme, bannerData } = this.state;
        // console.log(bannerData)
        let bannerList = [];
        if (bannerData.length > 0) {
            bannerData.map((item, index) => {
                bannerList.push(
                    <Carousel.Item key={index}>
                        {this.validImage(item.image_url, item.title)}
                        <a
                            href={item.target_url}
                            className="title"
                            style={{
                                color: theme.color.invertFont,
                                fontSize: theme.fontSize.title,
                                lineHeight: theme.lineHeight.title
                            }}
                        >
                            {item.title}
                        </a>
                    </Carousel.Item>
                )
            })
        } else {
            return <ContentLoader
                speed={2}
                viewBox="0 0 1519 474"
                backgroundColor={theme.color.loadingBG}
                foregroundColor={theme.color.loadingWave}
            >
                <rect x="0" y="0" rx="0" ry="0" width="1519" height="474" />
            </ContentLoader>
        }
        return bannerList;
    }

    componentDidMount = () => {
        if (this.state.bannerData.length <= 0) {
            fetchApi('banner', { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    // console.log(data)
                    this.setState({
                        bannerData: data
                    })
                })
        }
    }

    render() {
        const { theme } = this.state;
        return (
            <div className="banner">
                <Carousel
                    controls={false}
                    className="bannerbox">
                    {this.renderBanner()}
                </Carousel>
            </div>
        );
    }
}

export default Banner;