// Home->产品介绍
// 2020.7 LARE

import React, { Component } from 'react';
import '../styles/product.css';
import product from '../static/product';
import themePicker from '../theme/themeControl';
import { Col } from 'react-bootstrap';
import fetchApi from '../api/callApi';
import ContentLoader from "react-content-loader";

class Product extends Component {
    constructor(props) {
        super(props);
        this.state = {
            theme: themePicker(),
            productData1: [],
            productData2: [],
        }
    }

    validProduct = (img, name, intro, target) => {
        const { theme } = this.state;
        var ImgObj = new Image();
        ImgObj.src = img;
        let productItem = [];
        if (img && ImgObj.fileSize > 0 || (ImgObj.width > 0 && ImgObj.height > 0)) {
            productItem.push(
                <div className="showcase">
                    <img src={img} alt="Prodcut Image" />
                </div>
            )
        } else {
            productItem.push(
                <ContentLoader
                    key={0}
                    speed={2}
                    viewBox="0 0 522 225"
                    backgroundColor={theme.color.loadingBG}
                    foregroundColor={theme.color.loadingWave}
                >
                    <rect x="0" y="0" rx="5" ry="5" width="522" height="219" />
                </ContentLoader>
            )
        }
        if (name && name.length > 0) {
            productItem.push(
                <a
                    href={target}
                    className="productname"
                    style={{
                        color: theme.color.primaryFont,
                        fontSize: theme.fontSize.subtitle,
                        lineHeight: theme.lineHeight.subtitle
                    }}
                >
                    {name}
                </a>
            )
        } else {
            productItem.push(
                <ContentLoader
                    speed={2}
                    viewBox="0 0 522 40"
                    backgroundColor={theme.color.loadingBG}
                    foregroundColor={theme.color.loadingWave}
                >
                    <rect x="0" y="0" rx="5" ry="5" width="300" height="35" />
                </ContentLoader>
            )
        }
        if (intro && intro.length > 0) {
            productItem.push(
                <p
                    className="productintro"
                    style={{
                        color: theme.color.primaryFont,
                        fontSize: theme.fontSize.text,
                        lineHeight: theme.lineHeight.text
                    }}
                >
                    {intro}
                </p>
            )
        } else {
            productItem.push(
                <ContentLoader
                    speed={2}
                    viewBox="0 0 522 50"
                    backgroundColor={theme.color.loadingBG}
                    foregroundColor={theme.color.loadingWave}
                >
                    <rect x="0" y="0" rx="5" ry="5" width="522" height="20" />
                    <rect x="0" y="30" rx="5" ry="5" width="522" height="20" />
                </ContentLoader>
            )
        }

        return <Col xs={6} md={6}>
            {productItem}
        </Col>
    }

    renderProduct = () => {
        const { productData1, productData2 } = this.state;
        let productList = [];
        if (productData1.length > 0) {
            productList.push(
                this.validProduct(
                    productData1[0].image_url,
                    productData1[0].name,
                    productData1[0].introduction,
                    productData1[0].target_url
                )
            )
        } else {
            productList.push(
                this.validProduct("", "", "", "")
            )
        }
        if (productData2.length > 0) {
            productList.push(
                this.validProduct(
                    productData2[0].image_url,
                    productData2[0].name,
                    productData2[0].introduction,
                    productData2[0].target_url
                )
            )
        }
        return productList;
    }

    componentDidMount = () => {
        const { productData1, productData2 } = this.state;
        if (productData1.length <= 0) {
            fetchApi(`productSynopsis/1`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        productData1: data
                    })
                })
        }
        if (productData2.length <= 0) {
            fetchApi(`productSynopsis/2`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        productData2: data
                    })
                })
        }
    }

    render() {
        const { theme } = this.state;
        return (
            <div
                className="product"
                style={{
                    background: theme.color.primaryBG
                }}
            >
                <div className="container">
                    <p
                        className="pre"
                        style={{
                            color: theme.color.secondFont,
                            fontSize: theme.fontSize.assist,
                            lineHeight: theme.lineHeight.assist
                        }}
                    >{product.subtitle}</p>
                    <p
                        className="title"
                        style={{
                            color: theme.color.primary,
                            fontSize: theme.fontSize.subtitle,
                            lineHeight: theme.lineHeight.subtitle
                        }}
                    >{product.title}</p>
                    <p
                        className="intro"
                        style={{
                            color: theme.color.primaryFont,
                            fontSize: theme.fontSize.text,
                            lineHeight: theme.lineHeight.text
                        }}
                    >{product.intro}</p>
                    <a
                        href={`/product`}
                        className="more"
                        style={{
                            color: theme.color.primary,
                            fontSize: theme.fontSize.assist,
                            lineHeight: theme.lineHeight.assist,
                        }}

                    >进一步了解我们的更多产品 &gt;</a>
                    <div
                        className="productdisplay"
                    >
                        {this.renderProduct()}
                    </div>
                </div>
            </div>
        );
    }
}

export default Product;