import React, { Component } from 'react';
import { Col, Row } from 'react-bootstrap';
import themePicker from '../theme/themeControl';
import Header from '../components/Header';
import Footer from '../components/Footer';
import product from '../test/product';
import '../styles/product.css';
import fetchApi from '../api/callApi';
import ContentLoader from "react-content-loader";
import baseUrl from '../api/baseUrl';

class Products extends Component {
    constructor(props) {
        super(props);
        this.state = {
            theme: themePicker(),
            productData: []
        }
    }

    renderProduct = () => {
        const { theme, productData } = this.state;
        let productList = [];
        productData.map((item, index) => {
            productList.push(
                <Col
                    xs={9} md={6}
                    key={index}
                    className="productcard"
                >
                    <Col
                        xs={6} md={3}
                        className="productimg"
                    >
                        {item.image_url !== "0" ? <img src={baseUrl + item.image_url} alt={item.name} /> :
                            <ContentLoader
                                speed={0}
                                width={"100%"}
                                viewBox="0 0 100 100"
                                backgroundColor={theme.color.loadingBG}
                                foregroundColor={theme.color.loadingWave}
                            >
                                <rect x="0" y="0" rx="5" ry="5" width="100" height="100" />
                            </ContentLoader>
                        }
                    </Col>
                    <Col
                        xs={12} md={9}
                        className="producttext"
                    >
                        <p
                            style={{
                                color: theme.color.primaryFont,
                                fontSize: theme.fontSize.subtitle,
                                lineHeight: theme.lineHeight.subtitle
                            }}
                        >
                            {item.name}
                        </p>
                        <p
                            style={{
                                color: theme.color.primaryFont,
                                fontSize: theme.fontSize.text,
                                lineHeight: theme.lineHeight.text
                            }}
                        >
                            {item.introduction}
                        </p>
                        <a
                            href={"http://"+item.target_url}
                            style={{
                                color: theme.color.primary,
                                fontSize: theme.fontSize.text,
                                lineHeight: theme.lineHeight.text
                            }}
                        >
                            点击进入产品主页
                        </a>
                    </Col>
                </Col>
            )
        })
        return productList;
    }

    componentDidMount = () => {
        const { productData } = this.state;
        if (productData.length <= 0) {
            fetchApi(`product`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        productData: data
                    })
                })
        }
    }

    render() {
        const { theme } = this.state;
        return (
            <div
                className="products"
                style={{
                    background: theme.color.primaryBG
                }}
            >
                <Header />
                <div className="container">
                    <p
                        style={{
                            margin: "1rem 0",
                            color: theme.color.primaryFont,
                            fontSize: theme.fontSize.title,
                            lineHeight: theme.lineHeight.title
                        }}
                    >我们的产品</p>
                    <div className="productlist">
                        {this.renderProduct()}
                    </div>
                </div>
                <Footer />
            </div>
        );
    }
}

export default Products;