import React, { Component } from 'react';
import { Col, Row } from 'react-bootstrap';
import themePicker from '../theme/themeControl';
import Header from '../components/Header';
import Footer from '../components/Footer';
import Portal from '../components/Portal';
import notice from '../test/notice';
import '../styles/noticepage.css';
import fetchApi from '../api/callApi';
import ContentLoader from "react-content-loader";

const noticeTab = [
    {
        "tabName": "最新通知",
        "id": 1,
     },
    //   {
    //     "tabName": "理论答题",
    //     "id": 2,
    // }, {
    //     "tabName": "党建系统",
    //     "id": 3,
    // }
]

class Notice extends Component {
    constructor(props) {
        super(props);
        this.state = {
            currentTab: 0,
            theme: themePicker(),
            noticeData: []
        }
    }

    handleClickTab = (e) => {
        this.setState({
            currentTab: e
        })
    }

    renderTab = () => {
        const { theme } = this.state;
        let tabList = [];
        noticeTab.map((item, index) => {
            tabList.push(
                <span
                    key={index}
                    className="noticetabname"
                    onClick={() => this.handleClickTab(index)}
                    style={this.state.currentTab === index ?
                        {
                            color: theme.color.primary,
                            fontSize: theme.fontSize.text,
                            lineHeight: theme.lineHeight.text,
                            borderBottom: `${theme.color.primary} 2px solid`
                        } :
                        {
                            color: theme.color.primaryFont,
                            fontSize: theme.fontSize.text,
                            lineHeight: theme.lineHeight.text,
                        }
                    }
                >
                    {item.tabName}
                </span>
            )
        })
        return tabList;
    }

    renderNotice = (list) => {
        const { theme, noticeData } = this.state;
        let noticePaneList = [];
        noticeData.map((item, index) => {
            noticePaneList.push(
                <Row key={index}
                    className="noticeitem"
                >
                    {/* <Col xs={4} md={3}
                        className="noticeimg"
                    >
                        <img src="" alt=""/>
                    </Col> */}
                    <Col xs={9} md={9}
                        className="noticetext"
                    >
                        <a
                            href={`/article?labelId=${item.label_id}&id=${item.id}`}
                            style={{
                                color: theme.color.primaryFont,
                                fontSize: theme.fontSize.subtitle,
                                lineHeight: theme.lineHeight.subtitle
                            }}
                        >{item.title}</a>
                        <p
                            className="noticeintro"
                            style={{
                                color: theme.color.primaryFont,
                                fontSize: theme.fontSize.text,
                                lineHeight: theme.lineHeight.text
                            }}
                        >
                            {item.content ? item.content.substr(0, 100) : null}
                        </p>
                        <p
                            style={{
                                color: theme.color.secondFont,
                                fontSize: theme.fontSize.text,
                                lineHeight: theme.lineHeight.text
                            }}
                        >
                            <span
                                style={{ marginRight: "0.5rem" }}
                            >{item.time.substr(0, 10)}</span>
                            <span>{item.source}</span>
                        </p>
                    </Col>
                </Row>
            )
        })
        return noticePaneList;
    }

    componentDidMount = () => {
        const { noticeData } = this.state;
        if (noticeData.length <= 0) {
            fetchApi(`noticeList`, { method: "GET" })
                .then(res => res.json())
                .then((data) => {
                    this.setState({
                        noticeData: data
                    })
                })
        }
    }

    render() {
        const { theme } = this.state;
        return (
            <div
                className="noticepage"
                style={{
                    background: theme.color.primaryBG
                }}
            >
                <Header />
                <div className="container">
                    <p
                        style={{
                            margin: "1rem 0",
                            color: theme.color.primaryFont,
                            fontSize: theme.fontSize.title,
                            lineHeight: theme.lineHeight.title
                        }}
                    >通知公告 {this.renderTab()}</p>
                    <Row>
                        <Col xs={9} md={9}>
                            <div
                                className="noticelist"
                                style={this.state.currentTab === 0 ? {} : { display: "none" }}
                            >
                                {this.renderNotice(notice)}
                            </div>
                            <div
                                className="noticelist"
                                style={this.state.currentTab === 1 ? {} : { display: "none" }}
                            >
                                {this.renderNotice(notice)}
                            </div>
                            <div
                                className="noticelist"
                                style={this.state.currentTab === 2 ? {} : { display: "none" }}
                            >
                                {this.renderNotice(notice)}
                            </div>
                        </Col>
                        <Col xs={3} md={3} className="portalpart">
                            <div
                                className="sider"
                                style={{
                                    color: theme.color.primaryFont,
                                    fontSize: theme.fontSize.subtitle,
                                    lineHeight: theme.lineHeight.subtitle
                                }}
                            >快速入口</div>
                            <Portal />
                        </Col>
                    </Row>
                </div>
                <Footer />
            </div>
        );
    }
}

export default Notice;